<template>
  <div class="container">
    <header>
      <img src="../assets/images/profile_btnhdpi.png">
      <h1>Lottery <em>Double Color Balls</em></h1>
    </header>
    <section class="notice">
      <p><span>Upcoming lottery time</span><span>Mar 11, 2018 19:30</span></p>
      <strong>Choose 6 red balls + 1 blue balls to hit 5,000,000</strong>
    </section>
    <section class="balls-wrapper">
      <h2><span>Red Balls</span><span>6/6 Selected</span></h2>
      <ul class="redballs">
        <li :key="'red'+index" v-for="(ball,index) in redballs.balls" @click="selectball" :data-index="index" :data-balltype="'redballs'" :class="ball.value?'active':''">
          {{ ball.name }}
        </li>
      </ul>
      <h2><span>Blue Balls</span><span>3/1 Selected</span></h2>
      <ul class="blueballs">
        <li :key="'blue'+index" v-for="(ball,index) in blueballs.balls" @click="selectball" :data-index="index" :data-balltype="'blueballs'" :class="ball.value?'active':''">
          {{ ball.name }}
        </li>
      </ul>
      <section class="mult">
        <a v-if="!mult_show" class="tomult" @click="showMultCtrl"><img src="../assets/images/godown_btnhdpi.png"><span>Multiple Bets</span></a>
        <section class="multctrls"  v-if="mult_show">
          <span>Multiple Bets</span>
          <section class="multbtns">
            <a href="javascript:;">-</a>
            <b>1</b>
            <a href="javascript:;">+</a>
            <span>Times</span>
          </section>
        </section>
      </section>
    </section>
    <section class="btn-cont">
      <a class="btn">Add another ticket</a>
    </section>
    <section class="bbar">
      <section class="msg">Total:3 Ticket</section>
      <a href="javascript:;" class="btn">
        <span>Place Bet</span>
        <span>￡E 6.00</span>
      </a>
    </section>
  </div>
</template>

<script>
let redballs = Array.from({length:33}).map((item,i) =>{return {name:i+1,value:0}});
let blueballs = Array.from({length:16}).map((item,i) =>{return {name:i+1,value:0}});
export default {
  name: 'Index',
  data () {
    return {
      redballs : {
        balls : redballs,
      },
      blueballs : {
        balls : blueballs
      },
      mult_show:false
    }
  },
  methods:{
    selectball:function(e){
      let dataset = e.target.dataset;
      let t_ball = this.$data[dataset.balltype].balls[dataset.index];
      t_ball.value = t_ball.value?0:1;
    },
    showMultCtrl:function(){
      this.mult_show = true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../assets/css/index.scss"
</style>



